<template>
  <div class="table">
    <div class="table-title">
      <!-- 标题 -->
      <span class="first-title">参数设置</span>
      <span class="second-title">/ {{$route.query.mname}}</span>
      <!-- query 通过路由获取参数，路由参数见Home.vue -->
      <!-- Home中是通过getPareMenu向后端的getParaMenu申请的 -->
      <!-- <MenuItem v-for="item in paraMenu" :key="item.mid" :name="'3-'+item.mcode" :to="'table?mcode='+item.mcode+'&mid='+item.mid+'&mname='+item.name"> -->
    </div>

    <div class="subMenu-selector" style="width:200px; padding:0 0 20px 20px">
      <!-- 子菜单选择 -->
      <Select v-model="targetSub" @on-change="selectSubMenu">
        <!-- targetSub：sid， selectSubMenu：更新子菜单数据ParaList-->
        <Option v-for="(item, index) in allSubMenu" :key=index :value=item.sid>{{item.sname}}</Option>
        <!-- allSubMenu：包含指定mid下所有sid和sname信息 -->
      </Select>
    </div>
    
    <div class="table-spin">
      <!--  -->
      <div class="table-button">
        <Button v-show="mid === '3'" type="primary" class="button-set" @click="showDataModal">查看数据库</Button>
          <Modal 
            v-model="dataModalVisible"
            title="数据库数据"
            width="60%"
          >
              <!-- 横向表格展示 -->
              <Table
              :columns="columns"
              :data="databaseData"
              border
              :span-method="handleSpan"  
            >
          </Table>

          <!-- 底部按钮 -->
          <template #footer>
            <Button type="primary" @click="applyData">一键应用</Button>
            <Button @click="closeModal">关闭</Button>
          </template>

          </Modal>


        <Button v-show="mid === '3'" type="primary" class="button-set" @click="backUp">备份数据</Button>
<!-- !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!添加参数 -->
        <Button class="button-set" type="primary" @click="showModal = true">添加参数</Button>
        <Modal v-model="showModal" title="添加参数" width="600" :footer-hide="true">
            <paraform ref="paraform" @formSubmitted="handleFormSubmitted"></paraform>
        </Modal>

        <Button v-show="mid === '4'" class="button-set"  @click="showAdvancedParams = true" type="primary" style="background-color: #FF7D45; border-color: #FF7D45">高级参数</Button>
        <Modal
          v-model="showAdvancedParams"
          title="高级参数设置"
          width="650"
          :mask-closable="false"
          class="advanced-params-modal"
        >
          <div class="advanced-params-container">
            <!-- 参数组1 -->
            <div class="param-group">
              <div class="param-group-header">
                <Icon type="ios-analytics" class="param-group-icon" />
                <h3>采集参数</h3>
              </div>
              <div class="param-items">
                <div class="param-item">
                  <Checkbox v-model="params.highAccuracy" class="large-checkbox">
                    <span class="param-label">高精度模式</span>
                    <span class="param-desc">(提高数据精度但会降低采集速度)</span>
                  </Checkbox>
                </div>
                <div class="param-item">
                  <Checkbox v-model="params.enableDebugLog" class="large-checkbox">
                    <span class="param-label">启用失败自动采集</span>
                    <span class="param-desc">(失败后自动尝试重新采集)</span>
                  </Checkbox>
                </div>
              </div>
            </div>

            <!-- 参数组2 -->
            <div class="param-group">
              <div class="param-group-header">
                <Icon type="ios-globe" class="param-group-icon" />
                <h3>网络设置</h3>
              </div>
              <div class="param-items">
                <div class="param-item">
                  <Checkbox v-model="params.forceIPv4" class="large-checkbox">
                    <span class="param-label">使用IPv4</span>
                  </Checkbox>
                </div>
                <div class="param-item">
                  <Checkbox v-model="params.bypassProxy" class="large-checkbox">
                    <span class="param-label">绕过代理</span>
                    <span class="param-desc">(直接连接目标服务器)</span>
                  </Checkbox>
                </div>
              </div>
            </div>

            <!-- 数值型参数 -->
            <div class="param-group">
              <div class="param-group-header">
                <Icon type="ios-speedometer" class="param-group-icon" />
                <h3>性能调节</h3>
              </div>
              <div class="param-item-horizontal">
                <span class="param-label">采样间隔(ms):</span>
                <InputNumber 
                  v-model="params.sampleInterval" 
                  :min="10" 
                  :max="5000"
                  class="styled-input-number"
                />
                <span class="param-unit">毫秒</span>
              </div>
            </div>
          </div>

          <div slot="footer" class="modal-footer">
            <Button class="cancel-btn" @click="showAdvancedParams = false">取消</Button>
            <Button type="primary" class="confirm-btn" @click="saveAdvancedParams">保存设置</Button>
          </div>
        </Modal>
        
        <Button class="button-set" @click="setPara" type="primary">设置</Button>
        <!-- 设置:通过setPara改变数据库数据 -->
        <Button class="button-set" @click="queryParaAll">全部刷新</Button>
        <!-- 刷新：通过this.selectSubMenu()查询数据库最新数据 -->
        <Button v-show="mid === '4'" class="button-set" @click="startCollecting" type="primary">开始采集</Button>
        <!-- 开始采集 -->
      </div>

      <div class="table-content">
        <Table border stripe ref="paraTable" :columns="tcols" :data="tdata" :span-method="handleSpan" :key="DOMkey"></Table>
        <!-- border：表格边框 -->
        <!-- ref：引用信息（这里应该是直接获取 -->
        <!-- :columns="tcols" 第一行显示的每列名称-->
        <!-- :data="tdata" 每一列的具体内容 -->
        <!-- :span-method="handleSpan" 行/列合并需要设置属性 调用handleSpan方法 -->

        <Spin fix v-if="spinTrans">
            <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
            <div>数据加载中</div>
        </Spin>
      </div>
      
      <Spin size="large" fix v-if="spinShow"></Spin>
    </div>

  </div>
</template>

<script>
import paraform from './paraformView.vue';
import * as api from '@/api/index.js'

// 参数类型颜色映射表
const PARAM_TYPE_COLORS = {
  '功率参数': 'green',
  '电流参数': 'blue',
  '温度参数': 'red',

}
export default {
  name: 'paraTable',
  components: {
    paraform
  },
  data () {
    return {
      pollingInterval: null,//轮询间隔
      collectionStatus: 0, // 0-未开始 1-采集中 2-完成
      showAdvancedParams: false,
      params: {
        highAccuracy: false,
        enableDebugLog: false,
        forceIPv4: true,
        bypassProxy: false,
        sampleInterval: 100
      },
      mid: '',
      mcode: '',
      targetSub: '',
      allSubMenu: [],
      paraList:[],
      typeNumList: [],
      tselection: [],
      inputSetValue: {},
      selectRealData: [],
      // targetPara:[],
      spinShow: false, // 控制设置/查询时
      spinTrans: false, // 切换sid时
      DOMkey:0,
      showModal: false,
      dataModalVisible: false,  // 控制弹窗显示
      columns: [
        {
          title: '参数类型',
          key: 'type',
          align: 'center',
          render: (h, { row }) => {
            return h('Tag', { props: { color: PARAM_TYPE_COLORS[row.type] || 'default' } }, row.type || '无')
          },
        },
        {
          title: '参数名称',
          key: 'pname',
          align: 'center',
          render: (h, { row }) => {
            return h('strong', row.pname || '无')
          }
        },
        {
          title: '设置值',
          key: 'setValue',
          align: 'center',
          render: (h, { row }) => {
            return h('div', [
              h('Icon', { 
                props: { type: 'ios-alert', color: '#ff9900' },
                style: { marginRight: '5px' }
              }),
              h('span', row.setValue || '无')
            ])
          }
        }
      ],
      databaseData: [],       // 存储从数据库获取的数据
    }
  },
  created () {
    // 初始化
    this.mid = this.$route.query.mid
    this.mcode = this.$route.query.mcode
    // 获取该目录下的所有子目录
    this.getAllSubMenu()
  },
  beforeRouteUpdate (to, from, next) {//路由更新前也要重新获取mid\micode等参数
    console.log(to.query)
    this.mid = to.query.mid   //查询到访网址路径中的mid
    this.mcode = to.query.mcode
    // 获取该目录下的所有子目录
    this.getAllSubMenu()
    next()
  },
  computed: {
    tcols () {
      return [ 
        { // 表头固定
          title: '参数类型',
          key: 'type',
          align: 'center',
          width: 150
        },
        {
          title: '参数名称',
          align: 'center',
          key: 'pname'
        },
        {
          title: '查询值',
          align: 'center',
          key: 'readValue'
        },
        {
          title: '设置值',
          align: 'center',
          key: 'setValue',
          render: (h, params) => {
            if (params.row.writable === false) {  //属性不可写
              return h('span', params.row.setValue) //返回空白--当前行的writable变量
            } else {  //属性可写
              return h('Input', { // 输入框
                style: {
                  margin: '5px',
                  width: '90px'
                },
                props: {
                  type: 'text', //文本
                  value: params.row.setValue
                },
                on: { //事件
                  'on-blur': (event) => { // 失焦时（即鼠标离开该单元格）
                    let itemInput = JSON.stringify(event.target.value)
                    let flag = 0
                    if(event.target.value =="")
                    {
                        itemInput.replace(/""/g, "null");
                        event.target.value= JSON.parse(itemInput)
                        flag = flag+1
                        console.log("onblur-input:",event.target.value)
                    }
                    
                    params.row.setValue = event.target.value // 如果直接给paraList赋值，DOM会自动开始渲染，所以要先收集起来，后续赋值
                  
                      this.selectRealData.forEach((item) => { //遍历(selectRealData被修改：collectSelection--选择用户输入前的数据)
                        // selectRealData
                        if (item.pid == params.row.pid) {
                          item.setValue = params.row.setValue
                          flag = flag+1
                        }
                        // 更新数据
                  this.selectRealData[params.index].setValue = event.target.value
                      })

                    this.selectRealData = this.selectRealData.filter((item)=>item.setValue!=="")

                    if(flag==0){
                      this.selectRealData.push(params.row)
                    }
                    
                    console.log("selectRealData:",this.selectRealData)
                    
                  },
                  input: (event) => {
                    console.log("input1:",event)
                  },
                  'on-focus': () => {
                    // console.log("on-focus1:",event)
                    console.log("on-focus1")
                  }
                },
                nativeOn: {// 监听
                  'keyup': (event) => { // 输入enter时赋值
                    if (event.key.toLowerCase() === 'enter') {//toLowerCase() 方法用于把字符串转换为小写。
                      params.row.setValue = event.target.value 
                      // this.inputSetValue[params.row.pid.toString()] = params.row.setValue
                    }
                  },
                }
              })
            }
          }
        },
        {
          title: '单位',
          align: 'center',
          key: 'unit'
        },
        {
          title: '刷新时间',
          align: 'center',
          key: 'time'
        },
        {
        title: '操作',
        key: 'action',
        width: 80, // 根据按钮宽度调整
        render: (h, params) => {
          return h('Button', {
            props: {
              size: 'small'    // 按钮大小
            },
            style: {
              backgroundColor: '#FF4D4F', // 背景色
              borderColor: '#FF4D4F',     // 边框色
              color: '#FFFFFF'           // 文字颜色
            },
            on: {
              click: () => {
                // 处理按钮点击事件
                this.handleRowAction(params.row);
              }
            }
          }, '删除');
        }
      }
    ]
    },
    tdata () {
      return this.paraList
    },
    formattedData() {
      // 格式化数据以便更好显示
      return this.databaseData ? JSON.stringify(this.databaseData, null, 2) : '暂无数据';
    }
  },
  methods: {
    saveAdvancedParams() {
    this.$Message.success('高级参数已保存');
    this.showAdvancedParams = false;
    // 这里可以添加参数应用逻辑
    console.log('当前参数配置:', this.params);
  },
    applyData() {
      // 一键应用逻辑
      this.$Message.success('数据应用成功');
      // 这里可以添加实际的应用数据逻辑
      this.dataModalVisible = false;
    },
    closeModal() {
      this.dataModalVisible = false;
    },
    async showDataModal() {
      try {
        const TYPE_ORDER = {
          '功率参数': 1,
          '电流参数': 2,
          '温度参数': 3,
          '未知类型': 999 // 未知类型排在最后
        };
        // 格式化数据并按 type 排序
        this.databaseData = this.tdata
          .map((row) => ({
            type: row.type || '未知类型',
            pname: row.pname || '未知名称',
            setValue: row.setValue !== undefined ? row.setValue : '未设置'
          }))
          .sort((a, b) => {
            const orderA = TYPE_ORDER[a.type] || TYPE_ORDER['未知类型'];
            const orderB = TYPE_ORDER[b.type] || TYPE_ORDER['未知类型'];
            return orderA - orderB; // 按优先级升序排列
          });

        this.dataModalVisible = true;
        console.log(this.databaseData);
      } catch (error) {
        console.error('获取数据失败:', error);
        this.$Message.error('获取数据失败');
      }
    },
    backUp(){
      console.log("111")
    },
    // 删除某个参数
    deleteRowData(pcode){
      let arg = {
        pcode: Number(pcode)
      }
      console.log('删除参数的编号：', pcode);
      api.delParaValue(arg).then((res) => {  //获取参数列表
        if (res.code === 1) {
          this.$Message.success('数据删除成功');
          location.reload();
        } else {
          this.$Message.error(res.message)
        }
      })
    },
    handleRowAction(row) {
      this.$Modal.confirm({
        title: '确认提示',
        content: '确定要执行此操作吗？',
        onOk: () => {
          // 用户点击确定后执行的逻辑
          this.deleteRowData(row.pcode);
        },
        onCancel: () => {
          // 用户点击取消后的逻辑
          this.$Message.info('操作已取消');
        }
      });
    },
// 添加参数
    handleFormSubmitted () {
            this.showModal = false; // 关闭模态框
            this.selectSubMenu()  //设置后直接查询刷新一次
            location.reload();
        },


    getAllSubMenu () {
      let arg = {
        mid: Number(this.mid)
      }
      api.getSubMenu(arg).then((res) => {//获取所有子菜单
        if (res.code === 1) {
          this.allSubMenu = res.data.subMenu//所有子菜单
          this.targetSub = this.allSubMenu[0].sid // 目标子菜单 默认选择第一项
          this.selectSubMenu() // 获取选定的子菜单数据（更新了ParaList）
        } else {
          this.$Message.error(res.message)
        }
      })
    },
    selectSubMenu () {// **从数据库中获取表单数据（更新了ParaList）
      let arg = {
        sid: this.targetSub //当前子菜单
      }
      this.spinTrans = true
      api.getParaList(arg).then((res) => {  //获取参数列表
        if (res.code === 1) {
          this.paraList = res.data.paraList //需增加时间处理
          this.paraList.forEach(row => {
            if (row) row.setValue = "";
          });
          // console.log("paraList:",this.paraList)
          let typeObject = {}
          // 把type相同的放在一起
          this.paraList.forEach((item) => {
            if (typeof typeObject[item.type] === 'undefined') {//item.type为type_name
              typeObject[item.type] = [] //默认状态下置空
              typeObject[item.type].push(item)
              // push() 方法可向数组的末尾添加一个或多个元素，并返回新的长度。
            } else {
              typeObject[item.type].push(item)
            }
          })
          let newParaList = []
          for(let item in typeObject) {
            // 拼接
            newParaList = newParaList.concat(typeObject[item])// concat() 方法用于连接两个或多个字符串。
            this.typeNumList.push(typeObject[item].length) // 每个种类的个数
          }
          this.paraList = newParaList //*更新参数列表
          this.spinTrans = false  //*拼接结束标志符
        } else {
          this.$Message.error(res.message)
          this.spinTrans = false
        }
      })
    },
    // eslint-disable-next-line no-unused-vars
    handleSpan ({ row, column, rowIndex, columnIndex }) {
      // 该方法参数为 4 个对象：row: 当前行; column: 当前列; rowIndex: 当前行索引; columnIndex: 当前列索引
      // 该函数可以返回一个包含两个元素的数组，第一个元素代表rowspan，第二个元素代表colspan; 也可以返回一个键名为 rowspan 和 colspan 的对象。
      // 得知道有几个组
      if (this.typeNumList.length) {
        // selectSubMenu中更新 代表参数个数非0
        let typeNum = this.typeNumList.reduce((a, b) => {return a+b})
        for (let i = 0, index = 0; i <= typeNum - this.typeNumList[this.typeNumList.length-1]; i = i + this.typeNumList[index-1]) {
            if (columnIndex === 0 && rowIndex === i) {
              return {
                rowspan: this.typeNumList[index],
                colspan: 1
              }
            }
            for (let j = i; j < i + this.typeNumList[index]; j ++) {
              if (columnIndex === 0 && rowIndex === j) {
                return [0, 0]
              }
            }
            index ++
        }
      }
    },

    setPara () {

      for (let item of this.selectRealData) {
          if (isNaN(item.setValue) || !(item.setValue)) {//!Boolean(item.setValue),修改了，已经是布尔值了
            this.$Message.error({content:item.pname + '的设置值未输入或输入非数字，请重新输入', duration:3})
            return 0
          }
        // 根据longth判断大小
          if (Number(item.setValue) >= Math.pow(2, item.longth)) {  // Math.pow(底数x,指数y)
            this.$Message.error({content:item.pname + '的设置值超出限制，最大为'+ Math.pow(2, item.longth), duration:3})
          }
      }

      let arg = {  // Submenu作为请求参数
        // mid: Number(this.mid),// **哪块板
        // sid: Number(this.targetSub),// **哪个子菜单
        paraList: []
      }
      arg.paraList = this.selectRealData.map((item) => {
        return {
          pcode: item.pcode,
          setValue: item.setValue
        }
      })


      console.log("316set",arg)
      api.setParaValue(arg).then((res) => {//api：传输设置参数

        console.log(arg)
        if (res.code === 1) {
          this.$Loading.finish()  //加载进度条结束
          this.spinShow = false
          this.$Message.success('设置成功')
          // 清空所有设置值
          this.selectRealData = this.selectRealData.map(item => {
          return {
              ...item,
              setValue: ''  // 重置为空字符串
            }
          })
          // 设置：后端将设置数据帧传给数据端，获得成功消息后，成功则把设置值存入数据库，错误则返回code=0
          this.selectSubMenu()  //设置后直接查询刷新一次
          // this.updateParaItem // 重新获取从库里拉取一次, 更新表格
        } else {
          this.$Message.error(res.message)
          this.spinShow = false
          this.$Loading.error()
        }
      })
    },
    queryParaAll (){
      console.log("updateALL")
      this.selectRealData = this.paraList
      this.selectSubMenu()
      this.selectRealData = []
    },
    startCollecting (){
      let arg = this.paraList.map(row => row.readValue).slice(0, 15);   //暂定botda的开始采集传入farther_submenu_id = 8 对应数据库的 

      api.startCollect({arg}).then((res) => {  //获取参数列表
        console.log("开始采集状态",res)
      })
      this.$Message.success("开始采集");
      // 2. 开始轮询状态
      this.startPolling();
    },
    startPolling() {
      console.log("开始态")
    // 每2秒检查一次状态
    this.pollingInterval = setInterval(async () => {
      try {
        api.checkCollection().then((res) => {  //获取参数列表
            console.log("轮询采集状态",res)
            if (res.completed === 0) {
              // 3. 采集完成
              clearInterval(this.pollingInterval);
              this.collectionStatus = 2;
              this.$Message.success("采集完成");
              console.log("获得数据:",res.data)
              // 提交数据到vuex
              // api.getData().then(res => {
              //   console.log("获得数据:", res.data)
              //   this.$store.commit('setTableData', res.data) // 存储到 Vuex
              // })
            }
          })
      
      } catch (err) {
        console.error("轮询错误:", err);
        clearInterval(this.pollingInterval);
        this.collectionStatus = 0;
      }
    }, 2000);
  },
  }
}
</script>

<style lang="scss">
.demo-spin-icon-load{
    animation: ani-demo-spin 1s linear infinite;
}
@keyframes ani-demo-spin {
    from { transform: rotate(0deg);}
    50%  { transform: rotate(180deg);}
    to   { transform: rotate(360deg);}
}
.table {
  .table-spin {
    position: relative;
  }
  .table-title {
    padding: 20px 20px 10px 20px;
    text-align: left;
    .first-title {
      font-size: 22px;
      color: #464c5b;
      font-weight: bold;
    }
    .second-title {
      font-size: 16px;
      color: #464c5b;
      padding-left: 10px;
    }
  }
  .table-content {
    padding: 0 20px 0 20px;
    position: relative;
  }
  .table-button {
    display: flex;
    justify-content: flex-end;
    padding: 0 20px 10px 0;
    .button-set {
      margin-right: 10px;
    }
  }
}
/* 整体模态框样式 */
.advanced-params-modal {
  border-radius: 8px;
  overflow: hidden;
