import Vue from 'vue'
import Vuex from 'vuex'
import enums from '@/enum'

// 使用Vuex
Vue.use(Vuex)

export default new Vuex.Store({
  state: {// **前端共享数据
    role: enums.roleDis['NOLOG'],// **状态未登录（0）
    userName: '',// **用户名为空
    activeMenu: 0,// **可选菜单为0
    openNames: [],// **下拉菜单
  },
  mutations: {// **对共享数据进行操作
    setRole: function (state, n) {// **改变用户身份
      state.role = n
    },
    setName: function (state, n) {// **设置用户名
      state.userName = n
    },
    setActiveMenu (state, data) {// **设置可选菜单项
      state.activeMenu = data
    },
    setOpenNames (state, data) {// **设置下拉菜单
      state.openNames = data
    },
    // setTableData(state, data) {// **从后端C程序获取的数据
    //   state.tableData = data
    // }
  },
  actions: {
  },
  modules: {
  }
})
