import axios from 'axios'

axios.defaults.timeout =  6000

// 创建axios对象
const httpservice = axios.create({ // 这里根据情况修改
  timeout: 10000, // 设置超时时间 ms
  // 还可以加with-credencials和headers
})

// **拦截器设置：在请求或响应被then或catch处理前拦截他们(此处为响应拦截器)
httpservice.interceptors.response.use(
  function (response) {   // **对响应数据做些什么
    if (response.status === 200) {    // **status制度属性包含状态响应码，200为成功
      return response.data    // **成功，获取response中的data数据
    } else {
      return Promise.reject(response)
    }
  },// **这里实际上可以做如果返回未登录错误、返回登录页面的设置
  function (error) { // **对响应错误做些什么
    return Promise.reject(error)// **报错
})// **这里实际上可以根据status码不同书写不同的报错信息

// **Promise.reject()
// **返回一个指定value的失败的Promise对象，参数可以是一个Promise方法，也可以是一个值value

export default httpservice
