<template>
  <div class="chart">    
    <Row class="chart-title"><!-- 标题 -->
      数据曲线
    </Row>
    
    <Row><!-- 标签页选择 -->
      <Tabs v-model="mode">
        <TabPane label="温度数据" :name="dataMode.TEMP.toString()"></TabPane>
        <TabPane label="应变数据" :name="dataMode.STRAIN.toString()"></TabPane>
        <TabPane label="频移数据" :name="dataMode.FRE.toString()"></TabPane>
        <TabPane label="某点数据" :name="dataMode.ONE.toString()"></TabPane>
        <TabPane label="原始数据" :name="dataMode.ORIGIN.toString()" ></TabPane>
      </Tabs> 
    </Row>

    <Row>
      <div class="chart-selector">
        <!-- 根据 mode 决定显示下拉框还是输入框和按钮 -->
        <template v-if="mode!== dataMode.ONE.toString()">
          <Select v-model="displayTime" style="width:150px" @on-change="selectTime">
            <Option v-for="item in timeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </template>
        <template v-else>
          <div style="display: flex; align-items: center;">
            <Input v-model="oneinputValue" style="width: 150px; margin-right: 10px" />
            <Button @click="oneButtonClick">查看具体位置数据（m）</Button>
          </div>
        </template>
      </div>
    </Row>
    
    <Row>
    <div class="chart-wrap">
      <div class="chart-mask" v-if="loading&&trans"><!-- 加载且切换时显示遮罩loading -->
        <Spin fix>
          <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
          <div>加载中</div>
        </Spin>
      </div>
      <div class="chart-content"><!-- Echarts预留渲染盒 -->
        <div id="myChart"  class="echartBox" ref="chartRef" :style="{width: '100%', height: '100%'}">
        </div>
      </div>
    </div>
    </Row>

     <div class="chart-scope-title">
      {{zoomData.label}}
    </div>

    <div v-for="(item, index) in zoomData.axisInfo" :key=index style="display: flex; margin: 10px 0 10px 0; align-items: center">
      <!-- 分别渲染x轴y轴范围选择输入框 -->
      <span style="margin: 0 50px 0 30px">{{item.label}}</span>   <!-- 标签 -->
      <span style="margin: 0 10px 0 50px">从</span>               
      <Input v-model="item.from" style="width: 150px" />          <!-- from -->
      <span style="margin: 0 10px 0px 20px">到</span>
      <Input v-model="item.to" style="width: 150px" />            <!-- to -->

    </div>

    <div style="display:flex; flex-direction:row-reverse;margin-top:20px">
    <!-- 设置、恢复按钮 -->
      <Button style="margin: 0 25px 0 20px" @click="recvZoom">清空</Button>   <!-- 绑定recvZoom 恢复默认设置 -->
      <Button type="primary" @click="setZoom" >设置</Button>                  <!-- 绑定setZoom 更新数值 -->
    </div> 
  </div>
</template>

<script>

// @ is an alias to /src

import enums from '@/enum'
import * as api from '@/api/index.js'


export default {
  name: 'chartView',
  data () {
    return {
      loading: false, // 是否加载中
      trans: false, // 是否切换
      mode: enums.dataMode.FRE.toString(),
      oneinputValue : '', // 单个数据输入框值
      myChart: null,
      displayTime: 0,//下拉选择时间
      chartData: [],
      oneDatax: [],
      data3D:[],
      timer: 0, // 定时器
      // interval: 500000, // 轮询时间间隔
      interval: 600000, // 轮询时间间隔 10分钟
      // startIndex: 0,
      // endIndex: null,

      timeList:[{//日后这里会被数据库数据修改覆盖
        value: 0,
        label: '当前时间'
      }, {
        value: 1,
        label: '09:34',
      }, {
        value: 2,
        label: '10:00',
      }, {
        value: 3,
        label: '10:21',        
      }, {
        value: 4,
        label: '10:56',
      }, {
        value: 5,
        label: '11:15',   
      }],
      zoomData: { // 手动修改缩放区域
          label: "频移显示范围",
          axisInfo: [
            {
              label: "距离 （M）",
              from: null,
              to: null,
              type: 'x'
            },
            {
              label: "频移（无）",//需要修改
              from: null,
              to: null,
              type: 'y'
            }
          ]
      },
      actualZoom: [ // 设置后实际上的范围
        {
          type: 'x',
          to: null,
          from: null
        },
        {
          type: 'y',
          to: null,
          from: null
        },
        {
          type: 'z',
          to: null,
          from: null
        }
      ],
      zoomFlag: false, // 范围是否设置
      
    }
  },
  created () {    
    switch (this.mode) {
      case enums.dataMode.TEMP.toString():
        this.trans = true
        this.getFreshiftTimeList()
        break;
      case enums.dataMode.STRAIN.toString():
        this.trans = true
        this.getFreshiftTimeList()
        break;
      case enums.dataMode.FRE.toString():
        // this.chartData = this.$store.state.tableData;
        // console.log("this.chartData:",this.chartData)
        this.trans = true
        this.getFreshiftTimeList()
        break;
      case enums.dataMode.ONE.toString():
        this.trans = true
        break;
      case enums.dataMode.ORIGIN.toString():
        break;
      default:
        break;
    }
    
  },
  // 上面是刚一加载界面显示，下面时this.mode一改变就会显示
  mounted () {
    switch (this.mode) {
      case enums.dataMode.TEMP.toString():
        this.getTempData()
        break;
      case enums.dataMode.STRAIN.toString():
        this.getTempData()
        break;
      case enums.dataMode.FRE.toString():
        this.getFreshiftData()
        break;
      case enums.dataMode.ONE.toString():
        // this.chartData = []
        break;
      case enums.dataMode.ORIGIN.toString():
        this.getOriData()
        console.log(this.data3D.length);
        break;
      default:
        break;
    }
     
    
  },
  computed:{
    dataMode(){
      return enums.dataMode
    },
    chartTitle(){
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return '温度折线图'
        case enums.dataMode.STRAIN.toString():
          return '应变折线图'
        case enums.dataMode.FRE.toString():
          return '频率折线图'
        case enums.dataMode.ONE.toString():
          return '某点数据折线图'
        case enums.dataMode.ORIGIN.toString():
          return '原始图'
        default:
          return '' // 这个不写会报错，因为default没有返回值
      }
    },
    chartYname () {
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return '温度/℃'
        case enums.dataMode.STRAIN.toString():
          return '应变值'
        case enums.dataMode.FRE.toString():
          return '频率/kHz'
        case enums.dataMode.ONE.toString():
          return '频率/kHz'
        case enums.dataMode.ORIGIN.toString():
          return '原始图'
        default:
          return ''
      }
    },
    chartYunit () {
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return '℃'
        case enums.dataMode.FRE.toString():
          return 'kHz'
        case enums.dataMode.ONE.toString():
          return 'kHz'
        default:
          return ''
      }
    },
    chartXname () {
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return '距离/m'
        case enums.dataMode.FRE.toString():
          return '距离/m'
        case enums.dataMode.ONE.toString():
          return '时间'
        default:
          return ''
      }
    },
    limit () { // 告警值
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return 39
        case enums.dataMode.STRAIN.toString():
          return 15
        case enums.dataMode.FRE.toString():
          return 29//200
        case enums.dataMode.ONE.toString():
          return 20//200
        default:
          return 0;
      }    
    },
    lineOption () {


      const originalData = this.chartData; // 假设 this.chartData 是原始数据
      console.log("284:",originalData)
        let startIndex = 0;
        let endIndex = originalData.length - 1;

        // 处理 startIndex 和 endIndex 的情况，这里可以根据需要添加更复杂的逻辑
        if (this.startIndex!== undefined) {
            startIndex = Math.max(0, Math.min(this.startIndex, originalData.length - 1));
        }
        if (this.endIndex!== undefined) {
            endIndex = Math.max(0, Math.min(this.endIndex, originalData.length - 1));
        }
        if (startIndex > endIndex) {
            [startIndex, endIndex] = [endIndex, startIndex];
        }

        const pointCount = this.getPointCount();
        const data = this.filterData(originalData, pointCount, startIndex, endIndex);



      return {
        title: {//标题
          text: this.chartTitle, // 图名变量
          left: 'center',
        },
        tooltip: { // 鼠标悬停提示
          trigger: 'axis',  //以什么方式显示提示框.axis:坐标轴触发，主要在柱状图，折线图等会使用类目轴的图表中使用。
          triggerOn: 'click', // 只有在点击时才会触发 tooltip
          // formatter: `距离：{b0}m<br />${this.chartYname}：{c0}${this.chartYunit}`, //显示：距离：？？m（换行）{Y轴坐标}：数据{单位}
          formatter: (params) => {
                        const param = params[0];
                        return `距离: ${param.data[0]}m<br/>${this.chartYname}: ${param.data[1]}kHz`;
                    },
          extraCssText: 'text-align: left',// 改变提示框CSS格式
          padding: 10,
        },
        grid: { // 坐标系位置
          left: '3%',
          right: '8%',
          bottom: '10%',
          containLabel: true
        },
        toolbox: { // 工具盒 内置有导出图片、数据视图、动态类型切换、数据区域缩放、重置五个工具
          top: 0,
          right: 100,
          feature: {
            restore: {},  // 配置项还原
            saveAsImage: {} // 保存为图片
          },
        },
        xAxis: {
          type: 'category', // 设定好的离散值
          name: this.chartXname,
          // data: this.getXaxis(this.chartData),
          data: this.getXaxis(),
          min: 0,
          max: this.chartData.length - 1,
          boundaryGap: false,
          splitLine: {
            show: true,
            lineStyle: {
              color: '#999', // 网格线颜色
              width: 2 // 网格线宽度
            }
          }
        },
        yAxis: {
          name: this.chartYname,
          type: 'value',
          splitLine: {
            show: true,
            lineStyle: {
              color: '#999', // 网格线颜色
              width: 2 // 网格线宽度
            }
          }
        },
        visualMap: {  // 折线分层---阈值分层
          top: 0,
          right: 10,
          pieces: [{
              gt: 0,
              lte: this.limit,
              color: '#2b85e4'
          }, {
            gt: this.limit,
            color: 'red'
          }],
        },
        dataZoom: [{ // 数据区域缩放
          type: 'inside', // 鼠标滚轮缩放--没有inside只能缩放，无法进行鼠标滚动缩放和拖动 
          show: true,  
          xAxisIndex: 0,
          start: (startIndex / (originalData.length - 1)) * 100,
          end: (endIndex / (originalData.length - 1)) * 100,
          // end: 100,

        }, 
        {
          type: 'slider',
          xAxisIndex: 0,
          start: (startIndex / (originalData.length - 1)) * 100,
          end: (endIndex / (originalData.length - 1)) * 100
        },
        {
          type: 'slider', // y轴缩放条缩放
          yAxisIndex: 0,  //配置多个y轴使用，默认0
        }, 
        {
          type: 'inside', // x轴缩放条缩放
          yAxisIndex: 0,
        }
      ],
        series: [ // 数据
          {
            name: this.chartYname,
            type:'line',
            stack: '总量',
            sampling:"lttb",
            data: data,
            symbol:'emptyCircle', // 拐点设置为空心
            smooth: true,
            showAllSymbol: 'true',//已经处理过了
            symbolSize:5, //拐点大小
            color: '#2b85e4', 
            markLine: {// 警戒线
              symbol:"none",
              silent: true,
              label:{
                position:"start" // 将警示值放在哪个位置，三个值“start”,"middle","end"  开始 中点 结束
              },
              data: [{
                yAxis: this.limit,
                lineStyle:{ // 警戒线的样式 2b85e4   FA3934
                  color:"#2b85e4",
                },
              }]
            },
          },
        ],
      }
    },

    lineOption_3D () {
      return {
        tooltip: {},
        title: {//标题
          text: this.chartTitle, // 图名变量
          left: 'center',
        },
        backgroundColor: '#fff',
        toolbox: { // 工具盒 内置有导出图片、数据视图、动态类型切换、数据区域缩放、重置五个工具
          top: 0,
          right: 100,
          feature: {
            dataZoom: {   // 数据区域缩放。目前只支持直角坐标系的缩放
              yAxisIndex: 0,
              // 指定哪些 xAxis/yAxis被控制。如果缺省则控制所有的x轴/y轴。如果设置为 false 则不控制任何x轴。
              // 如果设置成 3 则控制 axisIndex 为 3 的x轴。如果设置为 [0, 3] 则控制 axisIndex 为 0 和 3 的x轴
              xAxisIndex: 0,
            },
            restore: {},  // 配置项还原
            saveAsImage: {} // 保存为图片
          },
        },
        visualMap: {
            show: false,
            dimension: 2,
            min: 0,
            max: 30,
            inRange: {
                color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
            }
        },
        xAxis3D: {
            type: 'value'
        },
        yAxis3D: {
            type: 'value'
        },
        zAxis3D: {
            type: 'value'
        },
        grid3D: {
            viewControl: {
                projection: 'orthographic'
            }
        },
        series: [{
            // type: 'scatter3D',
            type: 'surface',
            data: this.data3D,
            // lineStyle: {
            //     width: 4
            // }
        }]
      }
    },

  },
  beforeDestroy () {
    this.clearTimer()
  },
  watch: {
    mode (val) { //注意：刚开始不会执行； mode改变就执行不同获取数据函数
      // this.clearTimer() //清楚轮循查数据的定时器
      console.log('mode切换',this.mode)
      // let newLineOption = JSON.parse(JSON.stringify(this.lineOption))/*深拷贝*/
      let newLineOption
      let newLineOption_3D
      // let newLineOption_3D = JSON.parse(JSON.stringify(this.lineOption_3D))/*深拷贝*/
      switch (val) {
        case enums.dataMode.TEMP.toString():
          this.trans = true
          // this.getTempHttp(0)  //第一次申请温度数据在created当中，此刻申请切换后的当前数据
          newLineOption = JSON.parse(JSON.stringify(this.lineOption))
          this.myChart.clear()
          this.myChart.setOption(newLineOption,true)
          this.getTempData()   // 再次申请温度数据（定时器轮询）
          this.initZoom()         //初始化缩放空间
          this.updateChart(this.chartData)      //拷贝当前显示数据+更新之前判断是否有范围限制+显示

          this.zoomData.label = "温度显示范围"
          this.zoomData.axisInfo.forEach((item) => {
            if (item.type === 'y') {
              item.label = "温度（℃）"
            } // :todo z
          })

          break;
        case enums.dataMode.STRAIN.toString():
          this.trans = true
          newLineOption = JSON.parse(JSON.stringify(this.lineOption))
          this.myChart.clear()
          this.myChart.setOption(newLineOption,true)
          this.getTempData()   // 申请温度数据（定时器轮询）-> 包含getTempHttp(0) -> updataChart
          this.initZoom()         //初始化缩放空间
          this.updateChart(this.chartData)//
          
          this.zoomData.label = "应变显示范围"
          this.zoomData.axisInfo.forEach((item) => {
            if (item.type === 'y') {
              item.label = "应变（无）"
            } // :todo z
          })
          
          break; 
        
        case enums.dataMode.FRE.toString():
          this.trans = true
          newLineOption = JSON.parse(JSON.stringify(this.lineOption))
          this.myChart.clear()
          this.myChart.setOption(newLineOption,true)
          this.getFreshiftData()   // 申请温度数据（定时器轮询）-> 包含getTempHttp(0) -> updataChart
          this.initZoom()         //初始化缩放空间
          this.updateChart(this.chartData)//
          
          this.zoomData.label = "频移显示范围"
          this.zoomData.axisInfo.forEach((item) => {
            if (item.type === 'y') {
              item.label = "频率（Khz）"
            } // :todo z
          })
          
          break;
        case enums.dataMode.ONE.toString():
          this.trans = true
          newLineOption = JSON.parse(JSON.stringify(this.lineOption))
          this.myChart.clear()
          // this.myChart.setOption(newLineOption,true)
          // this.getFreshiftData()   // 申请温度数据（定时器轮询）-> 包含getTempHttp(0) -> updataChart
          // this.initZoom()         //初始化缩放空间
          // this.updateChart(this.chartData)//
          
          this.zoomData.label = "时间显示范围"
          this.zoomData.axisInfo.forEach((item) => {
            if (item.type === 'y') {
              item.label = "频率（Khz）"
            } // :todo z
          })
          
          break;
       case enums.dataMode.ORIGIN.toString():
          this.trans = true
          this.getOriData()
          newLineOption_3D = JSON.parse(JSON.stringify(this.lineOption_3D))/*深拷贝*/
          // 这里应该需要直接重新绘图，因为要画的是3D的
          // this.getOriginData()//原始数据
          // this.initZoom()//缩放空间

          console.log('3D',newLineOption_3D)
          this.myChart.clear()
          this.myChart.setOption(newLineOption_3D,true)
          break;
        default:
          break;
      }
      
       //用新的数据重新绘图，可以刷新成功
    }
  },
  methods: {
    initZoom() {
      this.zoomFlag = false
      this.actualZoom = [ 
        {
          type: 'x',
          to: null,
          from: null
        },
        {
          type: 'y',
          to: null,
          from: null
        },
        {
          type: 'z',
          to: null,
          from: null
        }
      ]
    },

    startChart () {
        this.myChart = this.$echarts.init(document.getElementById('myChart'));// 初始化
          // this.myChart.setOption(this.lineOption,window.onresize = myChart.resize)// 基于准备好的dom，初始化echarts实例
          //setOption:获取Echarts实例

        // 监听 dataZoom 事件
        this.myChart.on('dataZoom', () => {
          const option = this.myChart.getOption();
          const dataZoom = option.dataZoom[0];
          const start = dataZoom.start / 100 * (this.chartData.length - 1);
          const end = dataZoom.end / 100 * (this.chartData.length - 1);
          const ydataZoom = option.dataZoom[3];
          console.log('11569:',option.dataZoom[0]);
          console.log('11569:',option.dataZoom[3]);
          // const ystart = ydataZoom.start / 100 * Math.max(...this.chartData);
          // const yend = ydataZoom.end / 100 * Math.max(...this.chartData);
          const ystart = ydataZoom.startValue;
          const yend = ydataZoom.endValue;
          console.log('aa569:',Math.max(...this.chartData));
          console.log('569:',start, end, ystart, yend);
          this.updateChart(this.chartData, start, end,ystart,yend);
        });

        // 监听窗口大小变化事件
        window.addEventListener('resize', () => {
          this.myChart.resize();
          this.updateChart(this.chartData);
          console.log('resize!!!')
        });
          
        // 监听1、2、3图表的右键点击事件（跳转到具体点图表）----
          this.myChart.getZr().on('contextmenu', (params) => {
            // 阻止默认的右键菜单
            if (params.event && params.event.preventDefault) {
                params.event.preventDefault();
            }
            if (
                this.mode === this.dataMode.TEMP.toString() ||
                this.mode === this.dataMode.STRAIN.toString() ||
                this.mode === this.dataMode.FRE.toString()
              ){
                    // 获取点击的像素坐标
                const pointInPixel = [params.offsetX, params.offsetY];
                // 将像素坐标转换为数据点坐标
                const pointInGrid = this.myChart.convertFromPixel('grid', pointInPixel);

                // 判断是否点击在折线图的数据点上
                if (pointInGrid) {
                    const distance = pointInGrid[0]; // 获取 X 轴 距离值索引 
                    console.log('649右键点击的 X 轴距离:', distance);
                    this.myChart.clear();
                    this.mode = this.dataMode.ONE.toString(); // 切换到某点数据曲线图
                    this.oneinputValue = Math.ceil(distance); // 设置输入框的值 ----向上取整
                    this.oneButtonClick(); // 触发数据加载
                }
              }
            
        });
 
        
        this.myChart.setOption(this.lineOption,true);
            
    },
    getFreshiftTimeList () {
      api.getFreshiftTimeList().then((res) => {
        if (res.code === 1) {
          // console.log("res:",res)
          this.timeList = res.data.timeList.map((item) => {//获取时间列表
            return {value: ((new Date(item)).getTime() - 8 * 60 * 60 * 1000)/1000, label: item.toLocaleString()}
            })
          this.timeList.unshift({ // unshift() 方法可向数组的开头添加一个或更多元素，并返回新长度        
            value: 0,
            label: '当前时间'
          })
        } else if(res.code === 0) {
            this.$Message.error(res.message)
        }
      })
    },
    selectTime (target) { // 选择了历史事件，就不要使用定时器了
      this.clearTimer()//清空定时器
      if (target === 0) {//选中当前时间
        this.trans = false //不做改变
        switch (this.mode) {
          case enums.dataMode.TEMP.toString():
            this.getTempData() //不使用定时器时，用getTempHttp；用定时器时用getTempData
            break;
          case enums.dataMode.STRAIN.toString():
            this.getTempData()
            break;
          case enums.dataMode.FRE.toString():
            this.getFreshiftData()

            break;
          default:
            break;
        }
      } else {
        this.trans = true //发生改变
        // target= target
        switch (this.mode) {
          case enums.dataMode.TEMP.toString():
            this.getTempHttp(target)  //选中其他时间
            break;
          case enums.dataMode.STRAIN.toString():
            this.getTempHttp(target)
            // this.getStrainHttp(target)
            break;
          case enums.dataMode.FRE.toString():
            console.log("qita:",target)
            this.getFreshiftHttp(target)
            // this.getStrainHttp(target)
            break;
          default:
            break;
      }
     }
     this.trans = false
    },
    getXaxis () {//获取x轴坐标---根据不同的曲线界面
      let result = []
      // console.log("val.lengt")
      // console.log(this.chartData.length)
      if (this.mode == enums.dataMode.ONE.toString())
      {
        result = this.oneDatax;
      }else{
          for (let i = 0; i < this.chartData.length; i = i + 1) {
          result.push(i)// 这里直接以每个点间隔1m作为x轴坐标进行判定 
        }
      }
      
      return result
    },
    clearTimer () {
      console.log("Timer haven cleared!")
      // console.log(this.timer)
      clearInterval(this.timer) //在JavaScript中用于取消setInterval()函数设定的定时执行操作
    },
    getFreshiftData(){//查看当前数据
      this.clearTimer()
      this.getFreshiftHttp(0)
      console.log("Timer start")
      this.getFreshiftTimeList()
      // var _this = this;  //注意，要有，因为有箭头函数所以this作用域不在是vue，而定时器。我们要调用的是vue中的函数
      this.timer = setInterval(() => {  //定时器轮询 setInterval返回值可作为clearInterval清理定时器的参数。注意刚开始并不会执行
        this.trans = false  //{}内的为定时器执行代码
        console.log("getFREData0") 
        this.getFreshiftHttp(0)
      }, this.interval) //interval轮询时间，单位ms(_this.interval)
      // console.log("619;timer:",this.timer)
    },
    oneButtonClick() {
      // 处理按钮点击事件
      console.log('查看具体位置的数据随时间变化：', this.oneinputValue);
      this.clearTimer()
      console.log("Timer start")
      this.getOneHttp(this.oneinputValue)//length为输入框用户输入的数据位置
      this.timer = setInterval(() => {  //定时器轮询 setInterval返回值可作为clearInterval清理定时器的参数。注意刚开始并不会执行
        this.trans = false  //{}内的为定时器执行代码
        console.log("getFREData0") 
        this.getOneHttp(this.oneinputValue)
      }, this.interval) //interval轮询时间，单位ms(_this.interval)

    },
    getTempData(){//查看当前数据
      this.clearTimer()
      this.getTempHttp(0)
      console.log("Timer start")
      this.getFreshiftTimeList()
      // var _this = this;  //注意，要有，因为有箭头函数所以this作用域不在是vue，而定时器。我们要调用的是vue中的函数
      this.timer = setInterval(() => {  //定时器轮询 setInterval返回值可作为clearInterval清理定时器的参数。注意刚开始并不会执行
        this.trans = false  //{}内的为定时器执行代码
        console.log("getData0") 
        this.getTempHttp(0)
      }, this.interval) //interval轮询时间，单位ms(_this.interval)
      // console.log("619;timer:",this.timer)
    },
    getFreshiftHttp(time) {//通过时间参数查看历史记录（包括当前time=0）
        console.log("GetFREData--后台查询中time:",time)
        let arg = {
          "time" : time
        }
        this.loading = true
        api.getFreshiftData(arg).then((res) => {
          if (res.code === 1) {
            this.chartData = res.data.freshift
            console.log("794res:",res)
            // console.log("this.chartData",res)
            this.startChart ()//解决了还原按钮有问题？会显示空白
            this.loading = false
          } else if(res.code === 0) {
            this.$Message.error(res.message)
          }
        })
       
    },
    getTempHttp(time) {//通过时间参数查看历史记录（包括当前time=0）
        console.log("GetData--后台查询中")
        let arg = {
          "time" : time
        }
        this.loading = true
        console.log("arg:")
        api.getTempData(arg).then((res) => {
          if (res.code === 1) {
            this.chartData = res.data.temp
            this.startChart ()//解决了还原按钮有问题？会显示空白
            this.loading = false
          } else if(res.code === 0) {
            this.$Message.error(res.message)
          }
        })
       
    },
    getOneHttp(val) {//通过时间参数查看历史记录（包括当前time=0）
        console.log("789GetData--后台查询中")
        let arg = {
          "onedata" : val
        }
        this.loading = true
        api.getOneData(arg).then((res) => {
          if (res.code === 1) {
            // console.log(res.data.value)
            // console.log(res.data.time)
            // console.log(res.data)
            this.chartData = res.data.value
            this.oneDatax = res.data.time
            this.startChart ()//解决了还原按钮有问题？会显示空白
            this.loading = false
          } else if(res.code === 0) {
            this.$Message.error(res.message)
          }
        })
       
    },
    // 图表缩放功能-------------------------------
    // 根据屏幕宽度确定数据点数
    getPointCount() {
      const screenWidth = window.innerWidth - 540;//取出导航栏和图表边缘
      console.log('713:图表宽度',screenWidth);
      if (this.chartData.length > screenWidth) {
        return screenWidth;
      } else {
        return this.chartData.length;
      }
    },
    // 筛选指定范围内的数据点
    filterData(originalData, pointCount, startIndex, endIndex) {
      const rangeData = originalData.slice(startIndex, endIndex + 1);
      const interval = Math.ceil(rangeData.length * 2 / pointCount);
      const selectedData = [];

      for (let i = 0; i < rangeData.length; i += interval) {
        const subEndIndex = Math.min(i + interval, rangeData.length);
        const subArray = rangeData.slice(i, subEndIndex);
        let maxValue = -Infinity;
        let minValue = Infinity;
        let maxIndex = -1;
        let minIndex = -1;

        for (let j = 0; j < subArray.length; j++) {
          if (subArray[j] > maxValue) {
            maxValue = subArray[j];
            maxIndex = i + j + startIndex;
          }
          if (subArray[j] < minValue) {
            minValue = subArray[j];
            minIndex = i + j + startIndex;
          }
        }

        if (maxIndex < minIndex) {
          selectedData.push([maxIndex, maxValue]);
          selectedData.push([minIndex, minValue]);
        } else {
          selectedData.push([minIndex, minValue]);
          selectedData.push([maxIndex, maxValue]);
        }
      }

      return selectedData.slice(0, pointCount);
    },
    // 更新图表数据!!!!!!!!!!!!!!!!!!
    updateChart(originalData, startIndex = 0, endIndex = null, ystartIndex = 0, yendIndex = null) {
      const pointCount = this.getPointCount();
            console.log('763筛选出数据点数:',pointCount);
            
            if (endIndex === null) {
                endIndex = originalData.length - 1;
            }
            if (yendIndex === null) {
                yendIndex = Math.max(...originalData);
            }  
            console.log('765:',ystartIndex,yendIndex);           
            const data = this.filterData(originalData, pointCount, startIndex, endIndex);
            console.log('768:',data);
            const option = {
                tooltip: {
                    trigger: 'axis',
                    triggerOn: 'click', // 只有在点击时才会触发 tooltip
                    axisPointer: {
                        crossStyle: {
                            color: '#999'
                        }
                    },
                    formatter: (params) => {
                        const param = params[0];
                        return `距离: ${Math.floor(param.data[0])}m<br/>${this.chartYname}: ${param.data[1]}`;
                    },
                },
                xAxis: {
                    type: 'value',
                    min: 0,
                    max: originalData.length - 1,
                    boundaryGap: false,
                    splitLine: {
                    show: true,
                    lineStyle: {
                      color: '#999', // 网格线颜色
                      width: 2 // 网格线宽度
                    }
                  },
                },
                yAxis: {
                    type: 'value',
                    // min: 0,
                    // max: 100,
                    boundaryGap: false,
                    splitLine: {
                    show: true,
                    lineStyle: {
                      color: '#999', // 网格线颜色
                      width: 2 // 网格线宽度
                    }
                  }
                },
                dataZoom: [
                    {
                        type: 'slider',
                        xAxisIndex: 0,
                        start: startIndex / (originalData.length - 1) * 100,
                        end: endIndex / (originalData.length - 1) * 100
                    },
                    {
                        type: 'inside',
                        xAxisIndex: 0,
                        start: startIndex / (originalData.length - 1) * 100,
                        end: endIndex / (originalData.length - 1) * 100
                    },
                    {
                        type: 'slider',
                        yAxisIndex: 0,
                        start: ystartIndex / Math.max(...originalData) * 100,
                        end: yendIndex / Math.max(...originalData) * 100,
                        show: true,
                        realtime: true,
                        
                    },
                    {
                        type: 'inside',
                        yAxisIndex: 0,
                        start: ystartIndex / Math.max(...originalData) * 100,
                        end: yendIndex / Math.max(...originalData) * 100,

                    }
                ],
                series: [
                    {
                        data: data,
                        type: 'line',
                        symbol: 'circle',
                        symbolSize: 6,
                        showSymbol: true,
                        boundaryGap: false
                    }
                ]
            };

            this.myChart.setOption(option);
    
    },
    validateNumber(input) {
          if (input === 0) {
            return true;
        }
            return!isNaN(input) && input!== "" && Boolean(input);
        },
        handleError(message) {
            this.$Message.error(message);
        },
        validateRange(item, maxValue) {
            let isValid = true;
            const from = Number(item.from);
            const to = Number(item.to);

            if (from > maxValue) {
                this.handleError("设置值超出最大范围");
                isValid = false;
            } else if (to > maxValue) {
                this.handleError("曲线区域显示，设置值应在已有数值范围内");
                item.to = maxValue;
            } else if (from > to) {
                [item.from, item.to] = [item.to, item.from];
            } else if (from < 0 || to < 0) {
                this.handleError("设置值不可小于0");
                isValid = false;
            }
            return isValid;
        },
    //手动输入范围按钮
      setZoom() {
            let hasNaN = 0;
            const maxYValue = Math.max(...this.chartData);
            const maxXValue = this.chartData.length;

            this.zoomData.axisInfo.forEach((item) => {
                if (item.type === 'y') {
                    if (!this.validateNumber(item.to)) {
                        hasNaN++;
                        item.to = maxYValue;
                    }
                    if (!this.validateNumber(item.from)) {
                        hasNaN++;
                        item.from = 0;
                    }
                } else if (item.type === 'x') {
                    if (!this.validateNumber(item.to)) {
                        hasNaN++;
                        item.to = maxXValue;
                    }
                    if (!this.validateNumber(item.from)) {
                        hasNaN++;
                        item.from = 0;
                    }
                }
            });

            if (hasNaN > 0) {
                this.handleError("输入非数字或范围设置不完整，已自动修正");
            }

            let isValidRange = true;
            this.zoomData.axisInfo.forEach((item) => {
                const maxValue = item.type === 'y'? maxYValue : maxXValue;
                if (!this.validateRange(item, maxValue)) {
                    isValidRange = false;
                }
            });

            if (!isValidRange) {
                return 0;
            }

            this.zoomFlag = true;
            this.zoomData.axisInfo.forEach((item) => {
                const targetType = item.type;
                this.actualZoom.forEach((val) => {
                    if (val.type === targetType) {
                        val.to = Number(item.to);
                        val.from = Number(item.from);
                    }
                });
            });

            console.log('901:',this.actualZoom);
            this.updateChart(this.chartData,this.actualZoom[0].from,this.actualZoom[0].to,this.actualZoom[1].from,this.actualZoom[1].to);
        },
//清空范围设置
    recvZoom() {
      console.log("recvZoom--recover:"+'recover')
      this.initZoom() //初始化
      this.updateChart(this.chartData)  //更新
      this.zoomData.axisInfo.forEach((item) => {
          item.to = null
          item.from = null
      })
    },
    
    getOriData(){//3D
      // for (var t = 0; t < 25; t += 0.001) {
      //     var x = (1 + 0.25 * Math.cos(75 * t)) * Math.cos(t)
      //     var y = (1 + 0.25 * Math.cos(75 * t)) * Math.sin(t)
      //     var z = t + 2.0 * Math.sin(75 * t)
      //     this.data3D.push([x, y, z])
      //   }
      //   console.log(this.data3D.length);
      // for (var y = -50; y <= 50; y++) {
      //   for (var x = -50; x <= 50; x++) {
      //       var z = x + y;
      //       this.data3D.push([x, y, z]);
      // }
      let datax = []
      let datay = []
      let dataz = []
      for (var i = 0; i < 100; i += 1) {
          for(var t = 0;t < 100; t += 1){
            datax.push(i) 
            datay.push(t)
            dataz.push(Math.sin(t/10)*t/10)
          } 
      }

      for (var m = 0; m < 100; m += 1) {
          for(var n = 0;n < 100; n += 1){
            let num = (n+(m*100))
            this.data3D.push([datax[num], datay[num], dataz[num]])
          } 
      }
      console.log('data3D:',this.data3D)

    }
  },
}

</script>


