<template>
    <div class="map-area">
      <!-- <baidu-map class="map" :center="center" :zoom="zoom" :key="change"  :scroll-wheel-zoom="false"  @dragend="handleDragEnd"> -->
        <baidu-map class="map" :center="center" :zoom="zoom" :key="change"  :double-click-zoom="false" :scroll-wheel-zoom="false" :dragging="false"  @dragend="handleDragEnd" @ready="onMapReady">
        <bm-scale anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-scale>

        <!-- animation="BMAP_ANIMATION_BOUNCE"  -->
         <bm-marker 
          v-for="(item, index) in limitPonit" 
          :key=index 
          :position="item" 
          :dragging="false" 
          :icon="customIcon"
          :offset="{ width: 0, height: -8 }"
          @click="clickMark">
          
          <!-- <bm-label :content="item.value.toString() + '℃'" :labelStyle="{color: 'red', fontSize : '8px'}" :offset="{width: -35, height: 30}"/> -->

        </bm-marker>

        <!-- 底下感觉效果一样，并且 locationPoint点更多，画得更精确---以每一个测量点画线-->
        <bm-polyline :path="locationPoint" stroke-color="blue" :stroke-opacity="0.5" :stroke-weight="8" @click="clickPoint" @lineupdate="updatePolylinePath"></bm-polyline>

        <!-- <bm-polyline :path="PathPoint" stroke-color="blue" :stroke-opacity="0.5" :stroke-weight="8" @click="clickPoint" @lineupdate="updatePolylinePath"></bm-polyline> -->

      <bm-control :offset="{width: 20, height: 10}">
        <div class="control-panel">
          <button @click="toggleControlPanel" class="toggle-btn">
            {{ showControls ? '隐藏控制面板' : '显示控制面板' }}
          </button>
          
          <div v-if="showControls" class="control-file">
            <input
              type="file"
              ref="upload"
              accept=".xls,.xlsx"
              class="outputlist_upload"
            />
            
            <div class="control-buttons">
              <button @click="toggleDrawing" :class="{ active: isDrawing }">
                {{ isDrawing ? '结束画线' : '开始画线' }}
              </button>
              <button @click="undoLastPoint" :disabled="!canUndo">撤销上一点</button>
              <button @click="clearAll" :disabled="!hasDrawings">清除所有</button>
            </div>
          </div>
        </div>
      </bm-control>




        <bm-control :offset="{width: 10, height: 120}">
          <Card style="width:150px,">
              <div style="text-align:center">
                  <p>地图缩放</p>
                  <Button @click="changeZoom(1)">+</Button>
                  <Button type="primary" @click="changeZoom(0)"> 还原 </Button>
                  <Button @click="changeZoom(-1)">-</Button>
              </div>
          </Card>
          
        </bm-control>
      </baidu-map>

    </div>
</template>

<script >
/* global BMap */
import maskIcon from '../../public/mark.png';
import * as api from '@/api/index.js'
import { read, utils } from 'xlsx'
export default {
  data () {
    return {
      customIcon: {
        url: maskIcon,
        size: { width: 19, height: 25 },//只能在上边设置偏移
        // anchor: { width: 16 - 13, height: 32 + 13 }  // 向右上偏移
        // offset: { width: 115, height: 115 } 
      },
      center: {lng: 116.36729, lat: 39.967797},   // 地图显示中心,不是很关键,因为created函数里会根据数据库进行修改
      zoom: 18,    // 缩放水平
      chartData: [],
      interval: 500000,    //时间间隔
      timer: null,        //轮询定时器
      locationPoint: [],
      pointInfo: {lng: 116.406, lat: 39.915,dis: 0.007386155969105476, value: 17.5564}, //单点信息
      showWindow: false,    //是否显示
      limit: 39, // 设置告警值，
      limitPonit: {}, // 告警值数量 //bm-mark是可根据数据自动更新的
      targetLimit: {},  //告警点信息
      showLimit: false,
      datanum: 0,
      PathPoint: [],
      infoWindow: {
        contents1: '123',
        contents2: '456',
      },
      // value3: false,
      // sheetJson:[],
      pathdata:[],
      change:false,

      file:null,
      mapReady: false,

      // 手动画线
      map: null,
      isDrawing: false,
      points: [], // 存储当前线的所有点
      startMarker: null, // 仅存储起点标记
      currentPolyline: null, // 当前正在绘制的线
      polylines: [], // 存储所有完成的线

      showControls: false,  // 新增控制面板显示状态
    }
  },
  created () {

    // 数据库请求，进行修改  
    //利用 @ready 事件来保证 baidu-map 组件加载完成后再进行数据更新操作
    // this.getCenter()
    // this.getPath()
    // this.getTempData()

    this.$Notice.config({
      top: 50,
    });
  },
  mounted () {
    if (this.$refs.upload) {
      this.$refs.upload.addEventListener("change", (e) => {
      //绑定监听表格导入事件
      this.readExcel(e);
      // this.executeCommandAfterUpload();
      console.log("change：：", this.change);//不执行这一命令
    });
}
    
  },
  computed: {
    canUndo() {
      return this.isDrawing && this.points.length > 0
    },
    hasDrawings() {
      return this.polylines.length > 0 || this.points.length > 0
    }
  },
  beforeDestroy () {
    this.clearTimer()
  },
  methods: {
    toggleControlPanel() {
      this.showControls = !this.showControls;
    },
    // 添加起点标记
    addStartMarker(point) {
      // 清除已有起点标记
      if (this.startMarker) {
        this.map.removeOverlay(this.startMarker)
      }
      
      // 创建起点标记
      this.startMarker = new BMap.Marker(point, {
        enableMassClear: false
      })
      
      // 添加起点文字标签
      const label = new BMap.Label('起点', {
        position: point,
        offset: new BMap.Size(15, -15)
      })
      this.startMarker.setLabel(label)
      
      // 添加到地图
      this.map.addOverlay(this.startMarker)
    },
    toggleDrawing() {
      if (this.isDrawing) {
        this.stopDrawing()
      } else {
        this.startDrawing()
      }
      this.isDrawing = !this.isDrawing
    },
    
    startDrawing() {
      // this.locationPoint = [];
      // this.polylines = []
      this.clearCurrentDrawing()
      this.currentPolyline = new BMap.Polyline([], {
        strokecolor:'blue',
        strokeopacity:0.5,
        strokeweight:8
      })
      this.map.addOverlay(this.currentPolyline)
      this.map.addEventListener('click', this.handleMapClick)
    },
    
    stopDrawing() {
      this.map.removeEventListener('click', this.handleMapClick)
      // 移除所有折线覆盖物
      if (this.polylines.length > 0) {
          this.polylines.forEach(polyline => {
              this.map.removeOverlay(polyline);
          });
      }
      // 清空存储折线的数组
      this.polylines = [];
      if (this.points.length >= 2) {
        this.polylines.push(this.currentPolyline)
        console.log('1PathPoint:', this.PathPoint);
        this.PathPoint = this.points.map(point => ({ lng: point.lng, lat: point.lat }));
        console.log('2PathPoint:', this.points.map(point => ({ lng: point.lng, lat: point.lat })));
        this.setPoint() 
      } else {
        this.clearCurrentDrawing()
      }
      if (this.startMarker) {
        this.map.removeOverlay(this.startMarker)
        this.startMarker = null
      }
      // 打印鼠标画线所点的几个点
      // console.log('鼠标画线所点的点:', this.points.map(point => ({ lng: point.lng, lat: point.lat })));
      // 写入数据库中

      
      this.currentPolyline = null
      this.points = []
    },handleMapClick(e) {
      const point = e.point
      if (this.points.length === 0) {
        this.addStartMarker(point)
      }
      this.addPoint(point)
    },
    
    addPoint(point) {
      this.points.push(point)
      this.currentPolyline.setPath(this.points)
    },
    
    undoLastPoint() {
      if (this.points.length === 0) return
      this.points.pop()
      if (this.points.length === 0 && this.startMarker) {
        this.map.removeOverlay(this.startMarker)
        this.startMarker = null
      }
      this.currentPolyline.setPath(this.points)
    },
    
    clearCurrentDrawing() {
      if (this.currentPolyline) {
        this.map.removeOverlay(this.currentPolyline)
        this.currentPolyline = null
      }
      if (this.startMarker) {
        this.map.removeOverlay(this.startMarker)
        this.startMarker = null
      }
      this.points = []
    },
    
    clearAll() {
      this.limitPonit = {}
      this.locationPoint = []
      this.polylines.forEach(polyline => {
        this.map.removeOverlay(polyline)
      })
      this.polylines = []
      this.clearCurrentDrawing()
      if (this.isDrawing) {
        this.isDrawing = false
        this.map.removeEventListener('click', this.handleMapClick)
      }
    },


    onMapReady({map}) {
      this.mapReady = true
      this.map = map
      this.getCenter()
      this.getPath()
      this.getTempData()

    },
    handleDragEnd(event) {
    // 获取拖拽后的地图中心坐标
    const newCenter = event.target.getCenter();
    // console.log("114");
    // 更新 Vue 实例中的中心坐标
    this.center = { lat: newCenter.lat, lng: newCenter.lng };
    // console.log("114",this.center);
  },
    executeCommandAfterUpload() {
    // 在文件上传完成后执行的命令
    // 可以是任何需要延迟执行的操作
    this.change=1;
      console.log("change::", this.change);
  },
    update(){
      this.change=1;
      console.log("change::", this.change);
    },
    updatePolylinePath (e) {
      this.polylinePath = e.target.getPath()
    },
    changeZoom(val){
      let ex =this.zoom
      switch(val){
        case 1:
          ex= ex+1
          break;
        case 0:
          ex = 17
          break;
        case -1:
          ex = ex-1
          break;
      }
      if(ex < 17){
              ex = 17
      }else if(ex>18){
              ex = 18
      }
       console.log(ex)
      this.zoom = ex
      
    },
    // 读取excel中导入的数据
    readExcel(e) {
      // var that = this;
      const files = e.target.files;
      // 如果没有文件名
      if (files.length <= 0) {
        return false;
      } else if (!/\.(xls|xlsx)$/.test(files[0].name.toLowerCase())) {
        this.$Message.error("上传格式不正确，请上传xls或者xlsx格式");
        return false;
      }
      
      const fileReader = new FileReader();
    
      fileReader.onload = (ev) => {
        try {
          const data = ev.target.result;
          // 切换为新的调用方式
          const workbook = read(data, {
            type: "binary",
          });
          // 取第一张表
          const wsname = workbook.SheetNames[0];
          // 切换为新的调用方式 生成json表格内容
          const ws = utils.sheet_to_json(workbook.Sheets[wsname]);

          console.log("处理之后的文件数据是：：", ws); //  这里演示文件中的日期数据的处理
          // 后续为自己对ws数据的处理
          //  这里处理一下反斜杠日期的数字化问题
          // console.log(ws[0].经度)  //中文有点不适合  换成lng  、lat
          console.log("数据个数为：",ws.length,)
          console.log("插入经纬度坐标为:")
          this.center.lng = ws[0].clng
          this.center.lat = ws[0].clat
          this.zoom = ws[0].zoom
          console.log("325",this.center.lng, this.center.lat,this.zoom)
          for(let i=0; i<ws.length;i++){
            console.log("(",ws[i].lng,ws[i].lat,")")
            this.PathPoint = ws
          }
          console.log(this.PathPoint)
        } catch (e) {
          return false;
        }
        this.setPoint() 
      };
      fileReader.readAsBinaryString(files[0]);
    },
    setPoint(){
      let arg = {
        Clng:Number(this.center.lng),
        Clat:Number(this.center.lat),
        zoom:Number(this.zoom),
        pathdata:this.PathPoint
      }
      console.log(arg)

      api.setPoint(arg).then((res) => {  //获取参数列表
          if (res.code === 1) {
              console.log(res.message)
              // console.log('oook215')
              this.getCenter()
              this.getPath()
              this.getTempHttp(0)            
          } else {
            this.$Message.error(res.message)
          }
      })
    },
    getCenter() {//给地图中心点和缩放大小赋值
      api.getCenterPoint().then((res) => {
          if (res.code === 1) {
            // 数据库中修改查看
            this.center.lng = res.data.lng
            this.center.lat = res.data.lat
            this.zoom = res.data.zoom

            console.log('232',res)
          } else if(res.code === 0) {
            this.$Message.error(res.message)
          }
        })
        
    },
    getPath() {  
      api.getPathPoint().then((res) => {
          if (res.code === 1) {
            this.PathPoint = res.data.pathData
            console.log('244',res)
            this.getTempHttp(0)//能够使地图元素渲染更快
          } else if(res.code === 0) {
            this.$Message.error(res.message)
          }
        })
    },

    mockLocation () { //获取折线坐标列表
      console.log("polyline func inside")
      let locationPoint =[]
      let PathPoint = this.PathPoint//四个点三段
      console.log("inPoint:",PathPoint)
      
      let TotalPath = 0
      let dis = []
      for(let i = 0; i < PathPoint.length-1; i++)
      {
          let dislng = PathPoint[i+1].lng-PathPoint[i].lng
          let dislat = PathPoint[i+1].lat-PathPoint[i].lat
          // Math.pow(x,y)-- 返回底数的指定次幂    Math.sqrt  返回x的平方根
          let disab = Math.sqrt(Math.pow(dislng,2) + Math.pow(dislat,2))
          TotalPath += disab
          dis.push({lng:dislng,lat:dislat,ab:disab})
      }
      console.log("totalpath:",TotalPath)
      let count = this.datanum//分九段10个点
      let interval = TotalPath/(count-1)//每个点之间的间隔
      // let interval = TotalPath/100
      // let discount = 0
      for(let i = 0; i < PathPoint.length-1; i++)
      {
          dis[i].count = dis[i].ab/interval//每一段上有几个点
          dis[i].intlng = dis[i].lng/dis[i].count
          dis[i].intlat = dis[i].lat/dis[i].count
      }
      console.log("dis:",dis)
      for(let i =0;i <this.PathPoint.length-1; i++)//计算出每段路径上坐标点的数量，三段路径【i】
      {
        for(let j =0; j< dis[i].count; j++)//每段路径上有几个点，且计算出每个点的经纬度，存在locationPoint
            locationPoint.push({lng:Number((PathPoint[i].lng + j * dis[i].intlng).toFixed(6)), lat: Number((PathPoint[i].lat + j * dis[i].intlat).toFixed(6))})
      }
      while(locationPoint.length > count){
          locationPoint.pop()//移除数组 locationPoint 中的最后一个元素，并返回该元素的值，主要用到前半部分功能
      }      
      // console.log("301 locationPoint:",locationPoint)

      this.locationPoint = locationPoint//目前只有经纬度数据
    },

    clearTimer () {//清除定时器
      clearInterval(this.timer)
    },

    getTempHttp(time) {
        let arg = {
          "time" : time
        }
        api.getTempData(arg).then((res) => {
          if (res.code === 1) {
            
            this.chartData = res.data.temp//获取温度数据
            this.datanum = this.chartData.length
            // console.log("chart:",this.chartData)
            console.log("num:",this.datanum)
            
                console.log("polyline start")
                this.mockLocation()
                this.addTempData()  //让数据曲线和坐标点一一对应
                this.getLimitPoint()
                
                this.$nextTick(()=>{
                  this.change=!this.change
                  this.change=!this.change
                })

          } else if(res.code === 0) {
            this.$Message.error(res.message)
          }
        })

    },
    getTempData () { // 隔时查询温度数据--定时器
      this.timer = setInterval(() => {
        console.log("timer working")
        this.getTempHttp(0)
        this.addTempData()  //让数据曲线和坐标点一一对应----------------
        this.getLimitPoint()  // 在locationPoint中筛选大于limit阈值的点放入limitPoint中-----------------
      }, this.interval)

 
    },
    addTempData () { // 把数据信息放入点中——让chartData中的信息和locationPoint直接一一对应
      for (let i = 0; i < this.datanum; i++) {//this.chartData.length
        // 保留4位小数
        this.locationPoint[i].value = this.chartData[i].toFixed(4)
      }
      // console.log("addtemppath:2",this.locationPoint)//会执行在打印chart和num，所以刚开始是0，并且如果path里没有数据的话，mock方法里会算不出来locationPoint会报错
    },
    clickPoint (e) {
      // 取出点坐标，找出最相似的
      console.log("addtemppath:")
      let selectP = {//获取当前选中的坐标
        lng: e.point.lng,
        lat: e.point.lat
      }
      let distanceArr = this.locationPoint.map((item) => {
        // 算两点之间距离
        let dis = Math.sqrt(Math.pow((item.lng-selectP.lng),2) + Math.pow((item.lat - selectP.lat),2))
        // math.pow(x, y) 返回 x 的 y 次幂（次方）
        // dis=（经度差平方-纬度差平方）的开方，即当前点和列表点两点之间的直线距离

        return {//返回距离差
          dis: dis,
          lng: item.lng,
          lat: item.lat,
          value: item.value
        }
      })
      // 获得最接近的一个点，以此为选择点
      distanceArr.sort(this.compare('dis')) //sort：按照一定次序排列（dis越小越靠前）
      this.pointInfo = distanceArr[0]       //选择dis最小的一组信息作为pointInfo
      this.showWindow = true                //选择显示弹窗
      
      this.$Notice.destroy()
      this.infoWindowOpen(1)

      let ShowData = this.infoWindow.contents1 + '<br />' + this.infoWindow.contents2
      this.$Notice.info({
                    title: '当前坐标数据详情',
                    desc: ShowData,
                    duration: 0,
                    top:100
              });
    },
    
    compare(property){ //sort排序方法
        return function(a,b){ 
            var value1 = a[property]; 
            var value2 = b[property]; 
            return value1 - value2; 
        } 
    },
    getLimitPoint() {
      this.limitPonit = this.locationPoint.filter((item) => {return item.value > this.limit}) 
      // 在locationPoint中筛选大于limit阈值的点放入limitPoint中
    },
    infoWindowClose () {
      this.showWindow = false //不再显示气泡框
    },
    clickMark (e) { // 点击告警标识时
      this.targetLimit = this.limitPonit.find((item) => {return item.lng === e.target.point.lng && item.lat === e.target.point.lat})
      // 从limitPoint中寻找经纬度符合的点赋值给targetLimit
      this.showLimit = true
      // 显示告警

      this.$Notice.destroy()
      this.infoWindowOpen(2)

      let ShowData = this.infoWindow.contents1 + '<br />' + this.infoWindow.contents2
      this.$Notice.warning({
                    title: '告警点数据详情',
                    desc: ShowData,
                    duration: 0,
              });
    },
    infoWindowlimitClose () {
      this.showLimit = false
    },
    infoWindowOpen(val) {
      let info = []
      if(val===2)
          info = this.targetLimit
      else if(val===1)
          info = this.pointInfo
      this.infoWindow.contents1 = "【坐标】<br />lng："+String(info.lng)+"  lat："+String(info.lat)
      this.infoWindow.contents2 = "【温度】<br />"+String(info.value)+"℃"
      
      if(val===2)
          this.showLimit = true
      else if(val===1)
          this.showWindow = true
      
      
    },
  }
}
</script>

<style lang="scss" scoped>
.map-area {
  width: 100%;
  height: 95vh;
  position: relative;
  // display: flex;
  top: 0;
  left: 0;
  // bottom: 50px;
  // padding-bottom: 50px;
  // padding-top: 50px;
  box-sizing: border-box;
  .map {
    width: 100%;
    height: 100%;
    
  }
  .control-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.control-file input {
  padding: 3px 0px;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  min-width: 100px;
  transition: all 0.3s;
  font-weight: bold; /* 字体加粗 */
  color: #000000; /* 文字颜色 */
}
.control-buttons button {
  padding: 10px 20px;
  background: #27c23b;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  min-width: 100px;
  transition: all 0.3s;
  font-weight: bold; /* 字体加粗 */
  color: #000000; /* 文字颜色 */
}
.control-buttons button:hover {
  background: #f0f0f0;
}

.control-buttons button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.control-buttons button.active {
  background: #3388ff;
  color: white;
  border-color: #3388ff;
}
  // white-space: pre-wrap;
}

</style>