<template>
    <div class="login">
        <div class="login-card">
            <div class="login-card-title">
                欢迎登陆
            </div>
            <Divider></Divider>
            <Input class="login-card-input" prefix="ios-contact" placeholder="用户名" v-model="username"/>
            <Input class="login-card-input" type="password" prefix="ios-key" placeholder="密码" v-model="password"/>
            <div class="login-card-button">
                <Button class="login-button" type="primary" icon="md-checkmark" @click="checkLogin">登陆</Button>
                <Button class="clear-button" icon="md-close">重置</Button>
            </div>
            <div class="login-card-warn">
                创建新用户请联系管理员
            </div>
        </div>
    </div>
</template>
<script>
import * as api from '@/api/index.js'
export default {
    name: 'LoginView',
    components: {
        
    },
    data () {
        return {
            username: '',
            password: '',
            role: 3,
        }
    },
    methods: {
        checkInput () { // 检查是否输入完整
            if (this.username === '' || typeof this.username === 'undefined') {
                this.$Message.warning('请输入用户名')       // **$Message是viewUI的全局提示，轻量级反馈组件，会自动消失，warning/success/erro该是状态（样式）
                return false
            } else if (this.password === '' || typeof this.password === 'undefined') {
                this.$Message.warning('请输入密码')
                return false
            } else {
                return true
            }
        },
        checkLogin () {
            if (this.checkInput()) {
                let arg = {
                    name: this.username,
                    password: this.password
                }
                api.login(arg).then((res) => {// **res.data是从服务器端获取的数据
                    if (res.code === 1) {
                        this.role = res.data.role
                        this.$Message.success("登陆成功")
                        // vuex存储用户信息
                        this.$store.commit('setRole', this.role)
                        this.$store.commit('setName', this.username)
                        // 路由跳转到首页home
                        this.$router.push({name:'chart'})
                    } else {
                        // this.$router.push({name:'about'})
                        this.$Modal.error({//  **Modal是viewUI的模态对话框，在浮层显示，引导用户进行相关操作
                            title: '登陆失败',
                            content: res.message
                        });
                    }
                })
            }
        }
    }

}
</script>
<style lang="scss" scoped>
.login {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .login-card {
        width: 500px;
        height: 350px;
        border: 1px solid #dcdee2;
        border-radius: 20px;
        background-color: white;
        padding: 30px;
        .login-card-title {
            text-align: left;
            font-size: 18px;
            font-weight: 500;
            color: #464c5b;
        }
        .login-card-input {
            width: 280px;
            margin: 5px;
        }
        .login-card-button {
            margin: 20px 0;
            .login-button {
                width: 180px;
            }
            .clear-button {
                width: 80px;
                margin-left: 20px;
            }
        }
        .login-card-warn {
            margin-top: 60px;
            color: #808695;
        }
    }
}
</style>