import Chart from '../views/chart.vue'
import Map from '../views/mapView.vue'
import Table from '../views/table.vue'

import Update from '../views/updateView.vue'
// import Table from '../views/TableView.vue'

const routes =  [{
    path: '/chart',
    name: 'chart',
    component: Chart,
    meta: {
        requireAuth: true,
        title: '数据曲线',
        icon: 'ios-pulse',
    },
},
{
    path: '/map',
    name: 'map',
    component: Map,
    meta: {
        requireAuth: true,
        title: '地图',
        icon: 'ios-map',
    }
},
{
    path: '/table',
    name: 'paraTable',
    component: Table,
    meta: {
        requireAuth: true,
        title: '参数设置',
        icon: 'ios-construct',
    },
    children: [],
},
{
    path: '/update',
    name: 'update',
    component: Update,
    meta: {
        requireAuth: true,
        title: '系统更新',
        icon: 'ios-cloud-upload',
    }
}
]

export default routes
