<template>
  <div class="home">
    <div class="menu">
      <div class="menu-title">数据采集控制平台</div>
        <Menu theme="light" class="home-menu" :active-name="this.activeMenu">
          <!-- theme主题为light/dark -->
          <template v-for="(item, i) in menus">
            <MenuItem 
              v-if="!item.children" 
              :key="i" 
              :name="i" 
              :to="item.path.replace(/\//, '')"
            >
              <!-- 选择性渲染 如果没有children，直接渲染成MenuItem 否则再渲染成Submenu-->
              <!-- 把路径中的/path替换成path，方便跳转 -->
              <Icon :type="item.meta.icon" />
              {{ item.meta.title }}   <!-- 菜单标题 -->

            </MenuItem>

            <Submenu v-else :name="i" :key="i">
            <!-- v-if结果为false，渲染该项 -->
              <template slot="title">   <!-- 设置为嵌套的下拉菜单 -->
                <Icon :type="item.meta.icon" />
                {{ item.meta.title }}
              </template>
              
              <MenuItem 
                v-for="item in paraMenu" 
                :key="item.mid" 
                :name="'3-'+item.mcode" 
                :to="'table?mcode='+item.mcode+'&mid='+item.mid+'&mname='+item.name"
              >
              <!-- 从paraMenu中获取数据渲染 且将参数写入跳转地址 -->
                {{item.name}}
              </MenuItem>
            </Submenu>

          </template>
        </Menu>
      </div>
      <div class="content">
        <monitor></monitor>
        <router-view></router-view>
      </div>
  </div>
</template>

<script>
// @ is an alias to /src
import * as api from '@/api/index.js'
// import enums from '@/enum'
import {mapState} from 'vuex'
import Monitor from '@/components/monitor.vue'
import homeRoutes from '@/router/home'
// import router from '@/router'
import store from '@/store'

export default {
  name: 'HomeView',
  components: {
    Monitor
  },
  data () {
    return {
      paraMenu: [],
      menus: homeRoutes,//菜单内容参见homeRoutes
    }
  },
  created () {
    // console.log(this.activeMenu)
    this.getParaMenu()// 挂载/渲染开始前获取paraMenu数据
  },
  mounted(){
        window.addEventListener("beforeunload",() => {
          sessionStorage.setItem(store,JSON.stringify(this.$store.state));
          console.log('home：session重传state',store.state);
        })
        // if (!store.state.role || !store.state.userName) {// **如果vuex中role或username为0
        //   alert("请先登陆")// **告警提示
        //   this.$router.push({name:'login'})
        // } 
  },
  computed: {// 从vuex取出role用户身份、activeMenu选中菜单、openNames下拉菜单
    ...mapState([
      'role',
      'activeMenu',
      'openNames',
    ])
  },
  methods: {
    getParaMenu () { // 根据用户角色获取参数设置菜单
      const params = {
        role : this.role  //参数：角色身份
      }
      // console.log(params)
      api.getParaMenu(params).then((res) => {
        if(res.code === 1) {
          this.paraMenu = res.data.menu
        }
      })
    }
  }
}
</script>

