  height: 100%;
  display: flex;
  .menu {
    display: flex;
    flex-direction: column;
    height: 100%;
    .menu-title {
      font-size: 18px;
      color:black;
      background-color: #ecf9ff;
      padding: 20px;
      height: 68px;
    }
    .home-menu {
      height: 100%;
      padding-top: 20px;
      .ivu-menu-item {
        text-align: left;
      }
      .ivu-menu-submenu {
        text-align: left
      }
    }
  }
  .content {
    position: relative;
    background-color: #f8f8f9;
    border:2px;
    flex: 1;
  }
}
