<template>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="120" >
        <FormItem label="参数名称" prop="para_name">
            <Input v-model="formValidate.para_name" placeholder="输入参数名称"></Input>
        </FormItem>
          <FormItem label="参数代码" prop="para_code">
            <Input v-model="formValidate.para_code" placeholder="输入参数代码,四位数字例如5000，5001开始;保证唯一性"></Input>
        </FormItem>
        <FormItem label="参数类型" prop="type_name">
            <Select v-model="formValidate.type_name" placeholder="选择参数类型">
                <Option value="功率参数">功率参数</Option>
                <Option value="电流参数">电流参数</Option>
                <Option value="温度参数">温度参数</Option>
                <Option value="无">无</Option>
            </Select>
        </FormItem>
    <FormItem label="参数长度" prop="longth">
            <RadioGroup v-model="formValidate.longth">
                <Radio label="8">8位</Radio>
                <Radio label="16">16位</Radio>
              <Radio label="32">32位</Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label="是否可修改" prop="writeable">
            <RadioGroup v-model="formValidate.writeable">
                <Radio label="1">是</Radio>
                <Radio label="0">否</Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label="参数值" prop="write_value">
            <Input v-model="formValidate.write_value" placeholder="输入参数值"></Input>
        </FormItem>       
        <FormItem label="参数单位" prop="unit">
            <Input v-model="formValidate.unit" placeholder="输入参数单位可为空"></Input>
        </FormItem>
				<FormItem label="参数所属位置" prop="farther_submenu">
            <Select v-model="formValidate.farther_submenu" placeholder="选择所属位置">
                <Option value="1">采样参数</Option>
                <Option value="2">关系参数</Option>
                <Option value="3">洛伦兹系数</Option>
                <Option value="4">编码控制激光器</Option>
                <Option value="5">EDFA泵浦</Option>
                <Option value="6">SOA</Option>
                <Option value="7">探测器功率</Option>
                <Option value="8">botda</Option>
            </Select>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formValidate')">提交</Button>
            <Button @click="handleReset('formValidate')" style="margin-left: 8px">重置</Button>
        </FormItem>
    </Form>
</template>
<script>
import * as api from '@/api/index.js'
    export default {
        data () {
            return {
                formValidate: {
                    para_name: '',
                    para_code: '',
                    type_name: '',
                    longth: '32',
                    writeable:'',
                    write_value :'',
                    unit:'',
                    farther_submenu:'',
                },
              ruleValidate: {
                    para_name: [
                        { required: true,  message: '不可为空', trigger: 'blur' }
                    ],
                    para_code: [
                        { required: true, message: '不可为空', trigger: 'blur' },
                        { type: 'string', max: 4, message: '要求4位数字', trigger: 'blur' },
                        { type: 'string', min: 4, message: '要求4位数字', trigger: 'blur' }
                    ],
                    type_name: [
                        { required: true, message: '请选择参数类型', trigger: 'change' }
                    ],
                    longth: [
                        { required: true, message: '请选择参数长度', trigger: 'change' }
                    ],
                    writeable: [
						{ required: true, message: '请选择参数以后是否可以被修改', trigger: 'change' }
                    ],
                    write_value: [
                        { type: 'string', message: '可为空', trigger: 'blur' }
                    ],
                    unit: [
                        {type: 'string', message: '请输入参数单位，可为空', trigger: 'blur' }
                    ],
                    farther_submenu: [
                        { required: true, message: '请选择参数所属位置', trigger: 'change' }
                    ]
                }
                
            }
        },
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        
                        console.log(this.formValidate);

                        // 传递参数，打包---》后端请求
                        let arg = {  // Submenu作为请求参数
                            para_name:this.formValidate.para_name,
                            para_code:this.formValidate.para_code,
                            type_name: this.formValidate.type_name,
                            longth: this.formValidate.longth,
                            writable:this.formValidate.writeable,
                            write_value :this.formValidate.write_value,
                            unit:this.formValidate.unit,
                            farther_submenu:this.formValidate.farther_submenu,
                        }
                        api.addParaValue(arg).then((res) => {//api：传输设置参数
                            if (res.code === 1) {

                            // 可以加一个动态加载的动画



                            this.$emit('formSubmitted'); // 触发事件通知父组件
                            this.$Message.success('设置成功')
                            // 设置：后端将设置数据帧传给数据端，获得成功消息后，成功则把设置值存入数据库，错误则返回code=0
                            
                            // this.updateParaItem // 重新获取从库里拉取一次, 更新表格
                            } else {
                            this.$Message.error(res.message)
                            // this.spinShow = false
                            // this.$Loading.error()
                            }
                        })

                        
                        
                        

                    } else {
                        this.$Message.error('Fail!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
              this.$Message.success('Success!');
            }
        }
    }
</script>
